.landing-container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 90vh;
    position: relative;
}

.landing-container::after,
.landing-container::before {
    content: " ";
    width: 28.125rem;
    height: 28.125rem;
    border-radius: 28.125rem;
    background-color: #7c66e3;
    position: absolute;
    z-index: -1;
    filter: blur(225px);
}

.landing-container::after {
    top: -1rem;
    left: -5rem;
}

.landing-container::before {
    bottom: 2rem;
    right: 0;
}

.landing-content {
    flex: 1;
    transition: 2s;
    transform: translateX(-300px);
    opacity: 0;
}
.landing-content.show {
    transform: translateX(0);
    opacity: 1;
}

.landing-content h2 {
    font-size: 3.8rem;
    font-weight: 600;
    line-height: 5rem;
    margin-bottom: 1rem;
}

.landing-content p {
    width: 80%;
    font-size: 1rem;
    font-weight: 400;
    line-height: 2rem;
}

.landing-img {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem;
    margin-top: 5rem;
    transform: translateY(-30px);
    opacity: 0;
    transition: 2s;
}
.landing-img.show{
    opacity: 1;
    transform: translateY(30px);

}

.landing-img>div {
    display: flex;
    align-items: flex-end;
    gap: 2rem;

}

.landing-img>div>img {
    width: 25rem;
    transition: all 0.3s;

}

.tech-icon {
    width: 5rem;
    height: 5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    border-radius: 0.65rem;
    border: 1px solid #6852ba;
}

.tech-icon img {
    width: 3rem;
    height: auto;
    transition: 0.3s ease;
}

.landing-img>div>img:hover,
.tech-icon img:hover {
    transform: translateY(-0.5rem);
}

@media (max-width:1025px) {
    .landing-content h2 {
        font-size: 3rem;
        line-height: 4rem;
    }

    .landing-content p {
        font-size: .9rem;
        line-height: 1.6rem;
    }

    .landing-img>div>img {
        width: 20rem;
    }

    .tech-icon {
        width: 4rem;
        height: 4rem;
    }
}

@media (max-width:768px) {
    .landing-container {
        flex-direction: column;
    }

    .landing-img {
        margin: 2rem 0 2rem 0;
    }

    .landing-container h2 {
        margin-top: 15rem;
    }

    .landing-container h2,
    .landing-container p {
        width: auto;
        text-align: center
    }

    .landing-container::after,
    .landing-container::before {
        content: " ";
        width: 18rem;
        height: 18rem;
    }
    .landing-container::after{
        top: 0;
        left: 0;
    }
    .landing-container::before{
        bottom: 2rem;
        right: 0rem;
    }
}

@media (max-width:600px) {
    .landing-content h2{
        font-size: 2.2rem;
        line-height: 3rem;
    }
    .landing-content p{
        font-size: .8rem;
        line-height: 1.3rem;
    }
    .landing-img{
        gap:1rem;
    }
    .landing-img >div{
        gap: 1rem;
    }
    .landing-img > div > img{
        width: 16rem !important;
    }
    .tech-icon{
        width: 3.3rem;
        height: 3.3rem;
    }
    .tech-icon img{
        width: 2rem;
    }
}
